      flex: 4 1;
}

.right-section .section {
  padding: 16px 32px;
}

.footer {
  padding-top: 4px;
  color: #555;
}

.footer p {
  text-align: center;
  font-size: 0.7em;
  margin: 0;
}

.footer .skill {
  padding: 0 3px;
  border: 1px solid #eee;
}

@media screen {
  .Resume_container {
    max-width: 960px;
    margin: 0 auto;
