    border-right: 1px solid #eee;
    padding: 0;
    min-height: 100vh;
  }
}

.Resume_container {
  border: 1px solid #7B6ED6;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}

.roleName, .timeline, .school, .title, .skillGroupName, .presenter, .oss-project {
  font-size: 0.9em;
  color: #777;
  text-transform: uppercase;
}

.timeline {
  margin-left: auto;
  font-family: Inconsolata, Consolas, Menlo, monospace;
}

a {
  color: inherit;
}

h1 {
  color: #fff;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 12px;
  /* text-align: center; */
  /* text-shadow: 2px 4px 3px rgba(0,0,0,0.1); */
}

h3 {
  margin: 0;
  padding-bottom: 6px;
}

h3 .material-icons {
  font-size: 24px;
  vertical-align: top;
  margin-right: 6px;
}

h4 {
  display: inline-block;
}

.section:first-child h3 {
  margin-top: 12px;
}

